import React, { Component } from 'react'
import { Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import Form from './styles/Form'
import formatMoney from '../lib/formatMoney'

export default class CreateItem extends Component {
  state = {
    title: 'Cool Shoes',
    description: 'I love those Context',    
    image: 'dog.jpg',
    largeImage: 'doggo.jpg',
    price: 999
  }

  handleChange = (e) => {
    const {name, type, value} = e.target;
    const val = type === 'number' ? parseFloat(value) : value;
    this.setState({
        [name]: val
    })
  }

  render() {
    return <Form>
        <fieldset onSubmit={(e) => {e.preventDefault(); console.log(e)}}>
            <label htmlFor="title">
            Title
            <input 
                stype="text" 
                sid="title" 
                sname="title" 
                splaceholder="Title" 
                srequired 
                svalue={this.state.title} 
                sonChange={this.handleChange}/>
            </label>

            <label htmlFor="price">
            Price
            <input 
                stype="number" 
                sid="price" 
                sname="price" 
                splaceholder="Price" required 
                svalue={this.state.price} 
                sonChange={this.handleChange}/>
            </label>

            <label htmlFor="description">
            Description
            <textarea 
                sid="description" 
                sname="description" 
                splaceholder="Enter a description" 
                srequired value={this.state.description} 
                sonChange={this.handleChange}/>
            </label>
            
            <button>Submit</button>
        </fieldset>
    </Form>
  }
}
